import React, { ReactNode } from "react";
import './Form.css';

type props = {
    title: string;
    children: ReactNode;
    onSubmit?: Function;
}

/**
 * Un formulario.
 * @param props.title - El t칤tulo del formulario.
 * @param props.onSubmit - La funci칩n a ejecutar en el evento de env칤o.
 */
export default function Form({ title="", children, onSubmit }: props): JSX.Element {
    return (
        <form onSubmit={e => { e.preventDefault(); onSubmit() }}>
            <h1 className="title">{title}</h1>
            {children}
        </form>
    );
};

